<template>
    <div class="home" style="height: 100%;">
        <el-row style="height: 100%;">
            <el-col :span='3' style="min-height: 100%; background-color: #324057;">
                <el-menu
                    router
                    background-color="#324057"
                    text-color="#fff"
                    default-active="1-4-1" 
                    class="el-menu-vertical-demo" 
                    @open="handleOpen" 
                    @close="handleClose" 
                    :collapse="isCollapse" style="min-height: 100%;">
                    <el-menu-item index="home"><i class="el-icon-menu"></i>首页</el-menu-item>
                    <el-submenu index="2">
                        <template slot="title"><i class="el-icon-document"></i>客户管理</template>
                        <el-menu-item index="user">
                            <i class="el-icon-menu"></i>
                            <span slot="title">用户列表</span>
                        </el-menu-item>
                    </el-submenu>
                    <el-submenu index="3">
                        <template slot="title"><i class="el-icon-document"></i>订单信息</template>
                        <el-menu-item index="order">
                            <i class="el-icon-menu"></i>
                            <span slot="title">订单列表</span>
                        </el-menu-item>
                    </el-submenu>
                    <el-submenu index="4">
                        <template slot="title"><i class="el-icon-document"></i>广告信息</template>
                        <el-menu-item index="banner">
                            <i class="el-icon-menu"></i>
                            <span slot="title">广告列表</span>
                        </el-menu-item>
                    </el-submenu>
                    <el-submenu index="5">
                        <template slot="title"><i class="el-icon-document"></i>权限管理</template>
                        <el-menu-item index="adminUser">
                            <i class="el-icon-menu"></i>
                            <span slot="title">用户管理</span>
                        </el-menu-item>
                        <el-menu-item index="addAdminUser">
                            <i class="el-icon-menu"></i>
                            <span slot="title">增加用户</span>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-col>
            <el-col :span="21" class="content">
                <Head></Head>
                <div class="content_box">  
                    <router-view></router-view>
                </div>
            </el-col>
        </el-row>  
    </div>
</template>

<script>
    import Head from '../../components/head'
    export default {
        data() {
            return {
                isCollapse: false
            };
        },
        methods: {
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            }
        },
        mounted() {
        
        },
        components:{
            Head
        }
    }
</script>

<style scoped>
    .content_box {
        padding: 20px;
        background-color: #f0f2f5;
        position: relative;
        width: 100%;
        height: 100%;
    }
    .el-menu--dark .el-submenu .el-menu {
        background-color: #1f2d3d;
    }
</style>


